
import { defineComponent, reactive } from "vue"

export default defineComponent({
  setup() {
    const account = reactive({
      phone: "",
      verifyCode: ""
    })

    const rules = {
      phone: [
        {
          required: true,
          message: "手机号码是必填的~",
          trigger: "blur"
        },
        {
          pattern: /^[0-9]{11}$/,
          message: "手机号码是11位数字",
          trigger: "blur"
        }
      ],
      verifyCode: [
        {
          required: true,
          message: "验证码是必填的~",
          trigger: "blur"
        },
        {
          pattern: /^[0-9]{4}$/,
          message: "验证码4位数字",
          trigger: "blur"
        }
      ]
    }

    // const rules = {}

    return {
      account,
      rules
    }
  }
})
